import Code from "../../../../components/modules/code";
import { PageTitle } from "../../../../components/modules/pageTitle";

export const PHPConvertUnicode = () => {
  return (
    <>
      <PageTitle title="Chuyn chui Unicode thnh chui khng du"></PageTitle>
      <div className="row mt-2">
        <div className="col-12 col-sm-12 col-md-12">
            <Code
              code={`<?php
// M composer.json
// Thm vo trong "autoload" chui sau
// "files": [
// "app/function/function.php"
// ]
// Chy cmd : composer dumpautoload
function changeTitle($str,$strSymbol='-',$case=MB_CASE_LOWER){// MB_CASE_UPPER / MB_CASE_TITLE / MB_CASE_LOWER
    $str=trim($str);
    if ($str=="") return "";
    $str =str_replace('"','',$str);
    $str =str_replace("'",'',$str);
    $str = stripUnicode($str);
    $str = mb_convert_case($str,$case,'utf-8');
    $str = preg_replace('/[\\W|_]+/',$strSymbol,$str);
    return $str;
}
function stripUnicode($str){
    if(!$str) return '';
    //$str = str_replace($a, $b, $str);
    $unicode = array(
        'a'=>'|||||||||||||||||||||||',
        'A'=>'|||||||||||||||||||||||',
        'ae'=>'',
        'AE'=>'',
        'c'=>'||||',
        'C'=>'||||',
        'd'=>'|',
        'D'=>'|',
        'e'=>'|||||||||||||||',
        'E'=>'|||||||||||||||',
        'f'=>'',
        'F'=>'',
        'g'=>'|||',
        'G'=>'|||',
        'h'=>'|',
        'H'=>'|',
        'i'=>'|||||||||||',
        'I'=>'|||||||||||',
        'ij'=>'',
        'IJ'=>'',
        'j'=>'',
        'J'=>'',
        'k'=>'',
        'K'=>'',
        'l'=>'||||',
        'L'=>'||||',
        'o'=>'|||||||||||||||||||||||',
        'O'=>'|||||||||||||||||||||||',
        'Oe'=>'',
        'OE'=>'',
        'n'=>'||||',
        'N'=>'|||',
        'u'=>'||||||||||||||||||||||',
        'U'=>'||||||||||||||||||||||',
        's'=>'||',
        'R'=>'||',
        's'=>'|||||',
        'S'=>'|||',
        't'=>'||',
        'T'=>'||',
        'w'=>'',
        'W'=>'',
        'y'=>'||||||',
        'Y'=>'||||||',
        'z'=>'||',
        'Z'=>'||'
    );
    foreach($unicode as $khongdau=>$codau) {
        $arr=explode("|",$codau);
        $str = str_replace($arr,$khongdau,$str);
    }
    return $str;
}`}
              language="php"
            />
        </div>
      </div>
    </>
  );
};
